	module.exports = {"data":{"file":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":0.9657118055555556,"src":"/static/avatar-820706df6fdb07a9b021a790449fec17-381d6.jpg","srcSet":"/static/avatar-820706df6fdb07a9b021a790449fec17-62f05.jpg 200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-dc2b4.jpg 400w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-381d6.jpg 800w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-e5d70.jpg 1200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-d14e0.jpg 1600w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-9cc71.jpg 2225w","srcWebp":"/static/avatar-820706df6fdb07a9b021a790449fec17-fefde.webp","srcSetWebp":"/static/avatar-820706df6fdb07a9b021a790449fec17-5eed2.webp 200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-68e3c.webp 400w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-fefde.webp 800w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-300fe.webp 1200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-ed5bd.webp 1600w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-9442a.webp 2225w","sizes":"(max-width: 800px) 100vw, 800px"}}},"site":{"siteMetadata":{"title":"Kostas Bariotis' Blog","description":"Senior full stack engineer <a href=\"https://geekbot.io\">@geekbot_io</a>, co-organizer of <a href=\"https://devitconf.org\">@devitconf</a> & <a href=\"https://www.meetup.com/Thessaloniki-Node-js-Meetup/\">@skgnodejs</a>, host of <a href=\"http://devastation.tv\">Devastation Podcast</a>.","siteUrl":"https://kostasbariotis.com"}},"mainPost":{"html":"<p>Ο <a title=\"Brad Frost Web\" href=\"bradfrostweb.com\">Brad Frost</a>, απ' τους αγαπημένους μου σχεδιαστές, ταλαντούχος ομιλητής μας εισάγει σ' έναν κόσμο όπου ο καθένας μας είναι ελεύθερος να παράγει και να μοιράσει στους υπόλοιπους τα σκουπίδια του!</p>\n<p>[vimeo http://www.vimeo.com/63437853 w=500&#x26;h=281]</p>\n<p>Οι σημειώσεις που κράτησα από το βίντεο:</p>\n<ul>\n\t<li>10% του συνόλου των βιβλίων καθώς και 10% του συνόλου των βίντεο/φωτογραφιών του διαδικτύου παράχθηκαν μόλις <span style=\"text-decoration:underline;\">πέρσι!</span></li>\n\t<li>Μόλις το 35% του παγκόσμιου πληθυσμού έχει πρόσβαση στον παγκόσμιο ιστό. Οι υπόλοιποι κατευθύνονται κατά πάνω του με υπερφυσική ταχύτητα!</li>\n\t<li>Όταν η πληροφορία είναι φθηνή, η προσοχή της είναι ακριβή. Με άλλα λόγια η ποιότητα της πληροφορίας είναι αντιστρόφως ανάλογη της αποδοχής που θα λάβει! - <a title=\"James Gleick\" href=\"http://en.wikipedia.org/wiki/James_Gleick\">Jack Gleick</a></li>\n\t<li>90% του συνόλου είναι ΜΑΛΑΚΙΕΣ! -<a title=\"Sturgeon's Law\" href=\"http://en.wikipedia.org/wiki/Sturgeon's_Law\">Sturgeon's Law</a></li>\n</ul>","excerpt":"Ο  Brad Frost , απ' τους αγαπημένους μου σχεδιαστές, ταλαντούχος ομιλητής μας εισάγει σ' έναν κόσμο όπου ο καθένας μας είναι ελεύθερος να…","frontmatter":{"date":"April 08, 2013","path":"/peges-empneuses-102/","tags":null,"title":"Πηγες Εμπνευσης 102: Περι σκουπιδιων ο λογος","draft":null}},"nextPost":{"html":"<p>Τα \"παιχτρόνια\" της Google κάναν πάλι την δουλειά τους. Μια βόλτα στα στενά της επί δυο χρόνια εγκαταλελημένης περιοχής <a href=\"http://goo.gl/vdzSx\">Namie-machi</a>, που ανήκει στην νομαρχία της Fukushima, με το Google Car, αρκεί για ν' αποθανατήσουν τα σκηνικά της, που θυμίζει, τι άλλο φυσικά, αμερικάνικη ταινία!! Η περιοχή εγκαταλείφθηκε βιαστικά την άνοιξη του 2011, μετά από την αλληλουχία καταστροφών που υπέστη, πρώτα 9.0 Ρίχτερ σεισμός, αμέσως πελώρια κύματα να σε κυνηγούν και τέλος η διαρροή πυρηνικών ουσιών από την μονάδα πυρηνικής ενέργειας της Ιαπωνίας.</p>\n<p>[googlemaps https://maps.google.com/maps?q=%E6%97%A5%E6%9C%AC,+%E7%A6%8F%E5%B3%B6%E7%9C%8C%E5%8F%8C%E8%91%89%E9%83%A1%E6%B5%AA%E6%B1%9F%E7%94%BA&#x26;hl=en&#x26;ie=UTF8&#x26;sll=37.760834,140.474728&#x26;sspn=0.635121,1.234589&#x26;geocode=FTofPAIdzn9nCA&#x26;hnear=Namie,+Futaba+District,+Fukushima+Prefecture,+Japan&#x26;t=m&#x26;layer=c&#x26;cbll=37.492322,140.994431&#x26;panoid=9HpmyJMxaCzTMpcBE69eJA&#x26;cbp=12,351.09,,0,-1.33&#x26;hq=&#x26;ll=37.492226,140.994458&#x26;spn=0.03102,0.066047&#x26;z=14&#x26;source=embed&#x26;output=svembed&#x26;w=425&#x26;h=350]</p>\n<p>Πλέον όμως οι κάτοικοι που λησμόνησαν τις παλιές τους κατοικίες μπορούν με την βοήθεια του <a href=\"https://maps.google.com/maps?q=%E6%97%A5%E6%9C%AC,+%E7%A6%8F%E5%B3%B6%E7%9C%8C%E5%8F%8C%E8%91%89%E9%83%A1%E6%B5%AA%E6%B1%9F%E7%94%BA&amp;hl=en&amp;ie=UTF8&amp;ll=37.492226,140.994458&amp;spn=0.01992,0.038581&amp;sll=37.760834,140.474728&amp;sspn=0.635121,1.234589&amp;geocode=FTofPAIdzn9nCA&amp;hnear=Namie,+Futaba+District,+Fukushima+Prefecture,+Japan&amp;t=m&amp;z=15&amp;layer=c&amp;cbll=37.492322,140.994431&amp;panoid=9HpmyJMxaCzTMpcBE69eJA&amp;cbp=12,351.09,,0,-1.33\">Google Street Maps</a> να κάνουν μια βόλτα στις παλιές τους γειτονιές. Η αξία νομίζω είναι ανεκτίμητη κι αξίζει κάνεις να το δει!</p>","excerpt":"Τα \"παιχτρόνια\" της Google κάναν πάλι την δουλειά τους. Μια βόλτα στα στενά της επί δυο χρόνια εγκαταλελημένης περιοχής  Namie-machi , που…","frontmatter":{"date":"March 28, 2013","path":"/google-street-maps-fukushima/","tags":null,"title":"Google Street Maps στις γειτονίες της Fukushima","draft":null}}},"pathContext":{"mainPostPath":"/peges-empneuses-102/","nextPostPath":"/google-street-maps-fukushima/"}}

